import React, { useMemo, ReactElement } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps, Redirect } from 'react-router-dom';
import styles from '../../css/crosstable.module.css';

import Standings from './Standings';
import Leaderboard from './Leaderboard';
import { Player, TournamentSettings, TournamentStatus } from '../../interfaces';
import Knockout from './Knockout';

export default function StandingsRouter({
	players,
	settings,
	status,
	state,
	rProps,
	direction
}: {
	players: Player[]
	settings: TournamentSettings
	status: TournamentStatus
	state: string[]
	rProps: RouteComponentProps<any, StaticContext, any>
	direction: string
}) {

	const poolDistribution = useMemo(() => players.reduce((acc, curr) => {
		let pool = curr.pool || 0;
		if (!acc[pool]) acc[pool] = 0;
		acc[pool]++;
		return acc;
	}, [] as number[]), [players]);

	if (rProps.match.params.round === 'final') return <Redirect push to={{
		pathname: (rProps.match.url + (rProps.match.url.endsWith('/') ? '' : '/')).split('/').slice(0, -2).join('/') + '/' + settings.totalRounds,
		state: rProps.history.location.state
	}} />;
	if (rProps.match.params.round === 'individual') {
		return <Leaderboard
			status={status}
		/>
	};
	let round = parseInt(rProps.match.params.round);
	if (isNaN(round)) return null;
	if (round < 0 || round > settings.totalRounds) return null;

	let tables = [];
	for (let i = 0; i < poolDistribution.length; i++) {
		if (!poolDistribution[i]) continue;
		let component: ReactElement;
		if (settings.pairingSystem === 'knockout') component = <Knockout {...{ status, settings, }}	/>;
		else component = <Standings
			key={['Standings', i].join('.')}
			direction={direction.slice(6)}
			setDisplayPlayer={() => {}}
			show={() => {}}
			pool={i}
			{...{ status, players, settings, state, round }}
		/>
		tables.push(component);
	}
	tables.push(
		<section className={'animated-' + direction.slice(6)} key='footer.1'>
			<div className={styles.footer}>
				* denotes bonuses or requested byes granted.
			</div>
		</section>
	);
	return <>{tables}</>;

}